body {
    margin: 0;
}

.select-img-wrap {
    position: relative;
    width: 200px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    &:hover {
        cursor: pointer;
    }

    &:hover .select-img-border {
        border-width: 1px;
        border-color: white;
    }

    &.active .select-img-border {
        border-width: 4px;
        border-color: #00BEBE;
    }
}

.select-img-border {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;

    border: 1px solid rgba(255, 255, 255, 0.7);

    transition: all 0.1s;
}

.select-img {
    max-height: 200px;
    max-width: 200px;
    display: block;
}

.theme--dark.v-tabs-items {
    background-color: transparent !important;
}

.checkerboard {
    background: #eee url('data:image/svg+xml,\
    <svg xmlns="http://www.w3.org/2000/svg" width="400" height="400"         fill-opacity=".25" >\
             <rect x="200" width="200" height="200" />\
             <rect y="200" width="200" height="200" />\
             </svg>') !important;
    background-size: 20px 20px !important;
}
